*{
  margin: 0;
  padding: 0;
}
.App-input{
  text-align: center;
  background-color: brown;
  height: 300px;
  color: yellowgreen;
  font-size: 18px;
}
input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid blue;
  outline: none;
  padding: 10px 5px;
  width: 200px;

}

.App-input button{
  background-color: aqua;
  color: white;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 10px;
}

.navActive{
  color: blue;
}
nav{
  background-color: rgb(153,47,153);
  padding: 10px, 0;
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
nav ul{

  display: flex;
  list-style-type: none;
  justify-content: space-between;
  text-align: center;
  width: 40%;
  color: wheat;
  font-weight: bold;
  margin-left: 450px;
}
nav ul li{
cursor: pointer;
font-size: 17px;
transition: all 0.5s ease-in-out;
}

nav ul li:hover{
  transform: scale(1.4);
}
.App-container{
  background-color: aqua;
  color: rgb(239, 169, 41);
  padding: 15px;
}